.icon-gamburger:hover, .icon-gamburger:focus {
  stroke: var(--green-btn);
}

.header-menu-burger {
  background-color: rgba(0, 0, 0, 0);
  border-color: rgba(0, 0, 0, 0);
  padding: 0;
}

.page-home, .page-favorites {
  position: relative;
}

.page-home:hover, .page-home:focus, .page-favorites:hover, .page-favorites:focus {
  color: var(--green-btn);
  transition: color var(--transition-dur-and-func);
}

.page-home:before, .page-favorites:before {
  content: "";
  width: 100%;
  height: 1.8px;
  background-color: var(--green-btn);
  transition: transform var(--transition-dur-and-func);
  border-radius: 4px;
  display: block;
  position: absolute;
  top: -2px;
  left: 0;
  transform: scale(0);
}

.page-home:after, .page-favorites:after {
  content: "";
  width: 100%;
  height: 1.8px;
  background-color: var(--green-btn);
  transition: transform var(--transition-dur-and-func);
  border-radius: 4px;
  display: block;
  position: absolute;
  bottom: -2px;
  left: 0;
  transform: scale(0);
}

.page-navigation .current {
  color: var(--green-btn);
}

.shopping-link {
  fill: none;
  stroke: #050505;
}

.shopping-link:hover .shopping-cart, .shopping-link:focus .shopping-cart {
  stroke: var(--green-btn);
  fill: none;
  transition: var(--transition-dur-and-func);
}

.backdrop {
  transition: opacity var(--transition-dur-and-func);
  opacity: 1;
  background: rgba(46, 47, 66, .4);
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  overflow-y: scroll;
}

.backdrop.is-hidden {
  opacity: 0;
  pointer-events: none;
}

