  .switcher-toggle[type="checkbox"] {
    height: 0;
    width: 0;
    visibility: hidden;
    display: none;
  }

  .check-toggle {
    width: 46px;
    height: 20px;
    background-color: var(--light-theme-switcher-btn);
    cursor: pointer;
    border-radius: 18px;
    transition: all .3s cubic-bezier(.3, 0, .3, 1);
    display: block;
    position: relative;
  }

  .check-toggle:before {
    content: "";
    width: 18px;
    height: 18px;
    background-color: var(--light-theme-fill);
    border-radius: 18px;
    transition: all .3s cubic-bezier(.3, 0, .3, 1);
    position: absolute;
    top: 1px;
    left: 1px;
  }

  .switcher-toggle:checked + .check-toggle {
    background-color: var(--green-btn);
    transition: all .3s cubic-bezier(.3, 0, .3, 1);
  }

  .switcher-toggle:checked + .check-toggle:before {
    left: calc(100% - 1px);
    transform: translateX(-100%);
  }

  .check-toggle:active:before {
    width: 44px;
  }

  .header-buttonbox {
    justify-content: center;
    align-items: center;
    display: flex;
  }
}

